function getStyle(index) {
  const styles = [
    "col-span-2 hover:scale-95 transition duration-1000 h-[40rems] w-full relative",
    "hover:scale-95 transition duration-1000 h-[14rem] md:h-[30rem] relative",
    "hover:scale-95 transition duration-1000 ease-in-out h-[14rem] md:h-[30rem] w-full relative",
    // Add more styles as needed
  ];

  // Cycle through styles, assuming you have 3 styles
  return styles[index % 3];
}

// Define image-specific styles here
function getImageStyle(index) {
  const imageStyles = [
    "h-[30rem] object-cover w-full",
    "h-[14rem] md:h-[30rem] object-cover w-full",
    "h-[14rem] md:h-[30rem] object-cover w-full",
    // Add more image styles as needed
  ];

  // Cycle through image styles, assuming you have 3 image styles
  return imageStyles[index % 3];
}

